@font-face {
    font-family: "Montserrat-Arabic-Bold";
    src:url("./Noto_Kufi_Arabic/Montserrat-Arabic-Bold.ttf") 
}
@font-face {
    font-family: "Montserrat-Arabic-Light";
    src:url("./Noto_Kufi_Arabic/Montserrat-Arabic-Light.ttf") 
}
@font-face {
    font-family: "Montserrat-Arabic-Medium";
    src:url("./Noto_Kufi_Arabic/Montserrat-Arabic-Medium.ttf") 
}
@font-face {
    font-family: "Montserrat-Arabic-Regular";
    src:url("./Noto_Kufi_Arabic/Montserrat-Arabic-Regular.ttf") 
}
@font-face {
    font-family: "Montserrat-Arabic-SemiBold";
    src:url("./Noto_Kufi_Arabic/Montserrat-Arabic-SemiBold.ttf") 
}

*{
    margin: 0px;
    padding: 0px;
}
#root{
    overflow: hidden;
}
nav{
    width: 100%;
    height: 60px;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    padding-top: 10px;
}
.green-care-logo{
    width:50px;
}
.nav-menu{
    width: 60%;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 10%;
}
.nav-item{
    font-size: 20px;
    font-family: "Montserrat-Arabic-Bold";

}
@media (max-width:805px) {
    .nav{
        width: 90%;
        justify-content: left;
        padding-left: 20px;
    }
    .nav-menu{
        display: none;
    }
    
}

.hero{
    display: flex;
    justify-content: center;
    align-items: center;   
    padding-top: 75px; 
    padding-bottom: 75px;
    overflow: hidden;
}
.iphones{
    width: 700px;
    height: 700px;
}
.hero-title{
    width: 50%;
    color: #030000;
    text-align: center;
    font-family: "Montserrat-Arabic-Bold";
    font-size: 70px;
    font-style: normal;
    font-weight: 700;
}
.hero-title span{
    color: #14D75F;
}
@media (max-width:1250px) {
    .hero{ 
        padding-top: 50px; 
    }
    .iphones{
        width: 500px;
        height: 500px;
    }
    .hero-title{

        font-size: 60px;
    }
    
}
@media (max-width:950px) {
    .hero{
        display: flex;
        flex-direction: column-reverse; 
        padding-top: 20px; 
    }
    .hero-title{
        width: 80%;
    }
}
@media (max-width:550px) {
 
.hero{
    gap: -10%; 
    padding-top: 20px;
    padding-bottom: 0px;
}
.hero-title{
    width: 95%;
    font-size: 45px;
}
.iphones{
    width: 400px;
    height: 400px;
}
}

.border {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding-top: 100px;
    padding-bottom: 50px;
    gap: 100px;
}
.border-title{
    color: #030000;
    text-align: center;
    font-family: "Montserrat-Arabic-Bold";
    font-size: 70px;
    font-style: normal;
    font-weight: 700;
}
.borders{
    width: 80%;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    gap: 10%;
    row-gap: 50px;
}
.border-card{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items:end;
    gap: 15px;
}
.border-image{
    background-color:#14D75F ;
    width: 250px;
    height: 200px;
    border-radius: 20px;
}
.border-card-name{
    color: #030000;
    font-family: "Montserrat-Arabic-Medium";
    font-size: 15px;
}
.border-card-pos{
    color: #030000;
    font-family: "Montserrat-Arabic-Medium";
    font-size: 12px;
}
@media (max-width:780px) {
    .border-title{
        font-size: 50px;
    }
}
@media (max-width:580px) {
    .border-title{
        font-size: 40px;
    }
}


.fetur{
    display: flex;
    flex-direction: column;
    gap: 100px;
    justify-content: center;
    align-items: center;
    padding: 100px 0px;
}
.fetur-title{
    color: #030000;
    text-align: center;
    font-family: "Montserrat-Arabic-Bold";
    font-size: 70px;
    font-style: normal;
    font-weight: 700;
}
.fetur-container{
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    width:90%;
    height: 900px;
    background-color: #F3F3F3;
    border-radius: 50px;
}
.fetur-image{
    align-self: flex-end;
}
.fetur-menu{
    width: 50%;
    display: flex;
    flex-direction: column;
    justify-content: start;
    gap: 30px;
}
.fetur-item{
    width: 100%;
    padding: 20px 10px;
    border-radius: 20px;
    background-color: #fff;
    text-align: end;
    font-family: "Montserrat-Arabic-Bold";
    font-size: 20px;
}
@media (max-width:1200px) {
    .fetur-container{
        height: 600px;
    }
    .fetur-image{
        width: 350px;
    }
    .fetur-menu{
        width: 40%;
    }
    .fetur-item{
        font-size: 16px;
    }
}

@media (max-width:780px) {
    .fetur-container{
        height: 400px;
    }
    .fetur-image{
        width: 200px;
    }
    .fetur-menu{
        width: 50%;
        padding: 20px;
    }
    .fetur-item{
        font-size: 14px;
        text-align: center;
    }
    .fetur-title{
        font-size: 50px;
    }
}
@media (max-width:550px) {
    .fetur{
        gap: 50px;
        padding: 50px 0px;
    }
    .fetur-container{
        justify-content: center;
        gap: 10px;
        width:95%;
        height: 350px;
        border-radius: 30px;
    }
    .fetur-image{
        width: 150px;
    }
    .fetur-menu{
        gap: 20px;
    }
    .fetur-item{
        width: 100%;
        padding: 15px 5px;
        border-radius: 10px;
        background-color: #fff;
        text-align: end;
        font-family: "Montserrat-Arabic-Bold";
        font-size: 16px;
    }
}
@media (max-width:450px) {
    .fetur-item{
        font-size: 14px;
    }
}



.footer{
    width: 90%;
    padding: 100px 0px 50px;
    padding-left: 5%;
    padding-right: 5%;
    display: flex;
    flex-direction: column;
    justify-content: end;
    align-items: end;
    background-color: #030000;
    border-radius: 30px 30px 0px 0px;
    gap: 30px;
    overflow: hidden;
}
.footer-title{
    color: #14D75F;
    text-align: center;
    font-family: "Montserrat-Arabic-Bold";
    font-size: 70px;
    font-style: normal;
    font-weight: 700;
}
.list{
    display: flex;
    flex-direction: column;
    gap: 20px;
}
.list-item{
    text-align: right;
    color:#fff ;
    font-family: "Montserrat-Arabic-Regular";
    font-size: 16px;
    font-style: normal;
}
.copy-right{
    width: 100%;
    padding-top: 30px;
    text-align: center;
    color: #fff;
    font-family: "Montserrat-Arabic-Regular";
    font-size: 16px;
    font-style: normal;
    border-top: solid 1px #eee;
}

@media (max-width:550px) { 
    .footer-title{
        font-size: 50px;
    }
}
@media (max-width:450px) { 
    .footer-title{
        font-size: 40px;
    }
    .copy-right{
        font-size: 12px;
    }
}
